  border-bottom: 0.1rem solid #666666;
  margin-bottom: 0.8rem;
}

.form-main {
  padding: 1.5rem 0 3rem 0;
}

.form-group {
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
}

.form-group input {
  height: 2.5rem;
  background: transparent;
  outline: none;
  border: 0.1rem solid #666666;
}

.form-group .label {
  text-transform: uppercase;
  font-size: 1rem;
}

.form textarea {
  background: transparent;
  outline: none;
  border: 0.1rem solid #666666;
}

.form button {
  width: 100%;
  display: block;
  background: rgba(0, 0, 0, 0.3);
  outline: none;
  border: 0.1rem solid #666666;
  height: 2.5rem;
  font-size: 1rem;
  color: #fff;
  text-transform: uppercase;
  line-height: 2.5rem;
  CURSOR: POINTER;
}

