#toggle {
  width: 28px;
  height: 30px;
  position: fixed;
  right: 1rem;
  top: 1rem;
  z-index: 100;
  cursor: pointer;
}

#toggle div {
  width: 100%;
  height: 5px;
  background: white;
  margin: 4px auto;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.menuBox {
  position: fixed;
  top: 10%;
  left: 50%;
  -webkit-transform: translate(-50%, -10%);
          transform: translate(-50%, -10%);
  width: 800px;
  width: 80%;
  margin: auto;
  background: #262d7e;
  text-align: center;
  z-index: 1000;
  border-radius: 5px;

  opacity: 0;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
  pointer-events: none;
  box-shadow: 0 1px 5px #000;
}

.menuBox.open {

  -webkit-transform: translate(-50%, -50%);

          transform: translate(-50%, -50%);
  top: 50%;
  opacity: 1;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;

}

.menuBox-content {
  padding: 1rem;

}

.menuBox-content h1 {
  color: #fff;
  border-bottom: .1rem solid #cccccc;
  padding-bottom: .5rem;
  margin-bottom: 1.5rem;
}

.menuBox-content ul {
  list-style: none;
}

.menuBox-content li {
  margin-bottom: 1rem;
}

.menuBox-content a {
  text-decoration: none;
  color: #fff;
}
.overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(46, 49, 65, 0.8);
  z-index: 500;
  bottom: 0;
}
* {
  padding: 0;
  margin: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

html,
body {
  font-family: 'Mandali', sans-serif;
}
